.palette__container .myColor{text-transform:uppercase;font-family:"Poppins",sans-serif;font-size:1.25rem;outline:none;border:none;background:none;text-align:center;font-weight:bold}.palette__container .myColor:hover{cursor:pointer}.palette__container__item{position:relative;width:20vw;height:60vh}.palette__container__buttons{text-align:center;display:flex;flex-direction:column;align-items:center;justify-content:center;height:100%}.button{outline:none;background-color:transparent;border:none;font-family:"Poppins",sans-serif}.button:hover{cursor:pointer}.button--lock{margin-top:1.5rem;height:30px}.interactions{display:flex;flex-direction:column;align-items:center}.summary-values{text-transform:uppercase;font-family:"Poppins",sans-serif;font-size:1.25rem;width:50vw;outline:none;border:none;background:none;text-align:center;margin-top:2rem}.summary-values:hover{cursor:pointer}.button--refresh{color:white;font-size:2rem;font-weight:bold;background:linear-gradient(90deg, #6590FF 0%, #5C005E 100%);border-radius:10px;padding:.5rem;width:15rem;margin-top:2rem;transition:.3s}.button--refresh:active{transform:translateY(0.3rem)}.button--gradient{color:white;font-size:2rem;font-weight:bold;background:linear-gradient(90deg, blue 0%, crimson 100%);border-radius:10px;padding:.5rem;width:15rem;margin-top:2rem;transition:.3s;grid-column:1/4}.button--gradient:active{transform:translateY(0.3rem)}.palette__container{position:relative;display:flex;flex-direction:row;width:100vw;text-align:center}.header{display:flex;flex-direction:row;justify-content:space-between;align-items:center;width:90%;margin:1rem auto}.header h1{line-height:1;padding:.5rem}.button--info{color:white;font-size:1rem;height:2.5rem;padding:0 1rem;font-weight:bold;background:linear-gradient(90deg, #6590FF 0%, #5C005E 100%);border-radius:10px;transition:.3s}.button--info:active{transform:translateY(0.3rem)}.info{position:fixed;bottom:50%;left:50%;max-width:400px;min-width:300px;width:30%;box-shadow:0 0 10px gray;transform:translate(-50%, 50%);z-index:100;background-color:white;padding:1rem 1.5rem;border-radius:10px;text-align:left;list-style-position:inside}.info .button{position:absolute;top:.6rem;right:.6rem}.info .button--close{height:1rem;width:1rem}.info p{padding-top:.5rem}.info h3{padding-top:1rem;text-align:center}.navMenu{display:flex;flex-direction:row;justify-content:center;align-self:flex-end}.navMenu a{text-decoration:none;padding:0.5rem 1.5rem;margin:0 1rem;color:#737373;transition:.2s;font-weight:bold}.navMenu a:hover{color:#343434}.gradients{display:grid;grid-template-columns:repeat(3, 1fr);justify-items:center}.gradients>div{width:100%}.gradients .gradient{margin:1rem auto;width:15rem;height:15rem;border-radius:50%}.gradients .gradient-values{font-size:.75rem;font-family:"Poppins",sans-serif;width:100%;outline:none;border:none;background:none;text-align:center;margin-top:2rem}.gradients .gradient-values:hover{cursor:pointer}:root{box-sizing:border-box;font-size:1rem}*{margin:0;padding:0;box-sizing:inherit}body{font-family:"Poppins",sans-serif;color:#343434;overflow-x:hidden}.App{text-align:center}.showCopied{position:absolute;top:-4rem;padding:.5rem 2rem;background-color:white;box-shadow:0 0 10px green;border-radius:.5rem;left:50%;transform:translateX(-50%);color:forestgreen;-webkit-animation:none;animation:none}@-webkit-keyframes show{0%{top:-4rem}30%,70%{top:1rem}100%{top:-4rem}}@keyframes show{0%{top:-4rem}30%,70%{top:1rem}100%{top:-4rem}}.showCopied.toggle{-webkit-animation:show 2s ease-in-out;animation:show 2s ease-in-out}@keyframes show{0%{top:-4rem}30%,70%{top:1rem}100%{top:-4rem}}

